<template>
  <Form ref="loginForm" :model="loginForm" :rules="ruleValidate" :label-width="80">
    <Form-item label="" prop="account">
      <Input type="text" v-model="loginForm.account" />
    </Form-item>
    <Form-item label="" prop="password">
      <Input type="password" v-model="loginForm.password" />
    </Form-item>
            <FormItem label="" prop="role">
            <RadioGroup v-model="loginForm.role">
                <Radio label="admin"></Radio>
                <Radio label="user"></Radio>
            </RadioGroup>
        </FormItem>
    <Form-item>
      <Button type="primary" @click="handleLogin"></Button>
      <Button type="ghost" @click="handleReset('loginForm')" style="margin-left: 8px"></Button>
      <Button type="primary" @click="back()"></Button>
    </Form-item>
  </Form>
</template>
<script>
  import { userLogin,adminLogin } from '@/api/login';
  export default {
           data () {
            return {
                loginForm: {
                    account: '',
                    password: '',
                    role: ''
                },
                ruleValidate: {
                    account: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    role: [
                        { required: true, message: '', trigger: 'change' }
                    ]
                },
            }
        },
    methods: {
      handleLogin() {
        this.$refs.loginForm.validate(valid => {
          if (valid) {
            if (this.loginForm.role === 'admin') {
              adminLogin(this.loginForm.account, this.loginForm.password)
                .then(res => {
                  this.$Message.success("");
                })
            } else {
              userLogin(this.loginForm.account, this.loginForm.password)
                .then(res => {
                  this.$Message.success("");
                })
            }
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      },
      back() {
        this.$router.back();
      }
    }
  }
</script>



